* {
  box-sizing: border-box;
}

.note {
  color: grey;
  padding: 5px;
  font-size: 15px;
}

.note-btn {
  padding: 0.2em 0.5em;
  margin-left: 10px;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
